import React, { useState, useEffect } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Table,
  Header,
  HeaderRow,
  HeaderCell,
  Body,
  Row,
  Cell,
} from "@table-library/react-table-library/table";
import { useTheme } from "@table-library/react-table-library/theme";
import { Link } from "react-router-dom";

const THEME = {
  HeaderRow: `  
    font-weight: bold;
    font-size: 24px;
 
    background-color: #eaf5fd;
  `,
  Row: `
    font-size: 16px;
    
    &:hover {
      color: #212529;
      cursor: default;
    }
  `,
};

function Currency() {
  let [curritem, setCurritem] = useState([]);
  const [search, setSearch] = useState("");
  const theme = useTheme(THEME);

  useEffect(() => {
    async function fetchData() {
      const result = await axios.get("/api/getCurrency");
      //console.log("Result from api:");
      //console.log(result);
      setCurritem([...result.data]);
    }
    fetchData();
  }, []);
  const data = {
    nodes: curritem.filter((item) =>
      item.Name.toLowerCase().includes(search.toLowerCase())
    ),
  };
  return (
    <>
      <label htmlFor="search">
        Search by Name:
        <input
          id="search"
          type="text"
          onChange={(event) => {
            setSearch(event.target.value);
          }}
        />
      </label>
      <Table data={data} theme={theme} layout={{ custom: true }}>
        {(tableList) => (
          <>
            <Header>
              <HeaderRow>
                <HeaderCell style={{ width: "100px" }}>Item</HeaderCell>
                <HeaderCell style={{ width: "100px" }}>Price</HeaderCell>
              </HeaderRow>
            </Header>
            <Body>
              {tableList.map((cur, id) => (
                <Row key={id} item={cur} style={{ width: "200px" }}>
                  <Cell style={{ width: "100px" }}>
                    {<img src={cur.Image} alt={"Picture of " + cur.Name}></img>}{" "}
                    <Link to={`/Currency/${cur.Name}`} Id={cur.Id}>
                      {cur.Name}
                    </Link>
                  </Cell>
                  <Cell style={{ width: "100px" }}>{cur.Price}</Cell>
                </Row>
              ))}
            </Body>
          </>
        )}
      </Table>
    </>
  );
}

export default Currency;
